@import url(//maxcdn.bootstrapcdn.com/font-awesome/4.1.0/css/font-awesome.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.nav-link{font-size:14px;line-height:24px;margin-right:15px}.nav-link.active{color:#fec017}@media only screen and (min-width: 621px){.nav-link:hover{color:#c0a049}}

.app-title{display:flex;flex-direction:row;justify-content:space-between}.app-title h1{margin:0;font-weight:400}.app-title .search{position:absolute;right:32px;top:20px;color:#aaa;font-size:16px;width:280px}.app-title input{height:40px;padding:8px;font-size:16px;line-height:24px;text-indent:32px;width:100%;color:black}.app-title .search .fa-search{position:absolute;top:13px;left:10px;color:#252b40}@media only screen and (max-width: 700px){.app-title{display:flex;flex-direction:column;justify-content:center;align-items:center}.app-title .search{position:relative;right:0;top:20px;color:#aaa;font-size:16px;width:100%}}.app-header{background:#252b40;padding:32px 32px 16px 32px}.app-links{margin-top:30px;cursor:pointer}@media only screen and (max-width: 700px){.app-links{display:flex;flex-wrap:wrap;justify-content:center;cursor:none}}

.music-event-box{min-width:150px;filter:drop-shadow(0px 1px 2px rgba(0,0,0,0.25));cursor:pointer;transition:all 700ms}.music-event-box img{height:100%;width:100%;object-fit:contain}@media only screen and (max-width: 621px){.music-event-box{margin:0}}.music-event-box:after{content:"";position:absolute;width:100%;height:100%;top:0;left:0;background:rgba(0,0,0,0.6);opacity:0;transition:all 0.5s;-webkit-transition:all 0.5s}@media only screen and (min-width: 621px){.music-event-box:hover:after{opacity:1}}.triangle{width:0;height:0;border-style:solid;border-width:0 10px 10px 10px;border-color:transparent transparent #252b40 transparent;position:absolute;bottom:-17px;left:0;right:0;margin-left:auto;margin-right:auto}

.music-event-details__block{background-color:#252b40;grid-column:1 / -1;height:400px;position:relative;left:calc(-50vw + 50%);width:100vw;transition:height 700ms;display:flex;filter:drop-shadow(0px 1px 2px rgba(0,0,0,0.25))}@media only screen and (max-width: 1080px){.music-event-details__block{height:auto !important}}@media only screen and (max-width: 700px){.music-event-details__block{flex-direction:column-reverse}}.music-event-details__info{padding:32px;flex:6 1;display:flex;flex-direction:column}.music-event-details__info h1{margin:0;margin-bottom:25px}@media only screen and (max-width: 700px){.music-event-details__info h1{font-size:1.2em !important}}.music-event-details__date{margin-bottom:10px}.music-event-details__icon{margin-right:10px}.music-event-details__text{margin-top:30px;font-size:1vw;margin-bottom:10px}@media only screen and (max-width: 1080px){.music-event-details__text{font-size:0.8em}}@media only screen and (max-width: 700px){.music-event-details__text{font-size:0.8em}.music-event-details__date{font-size:0.9em}.music-event-details__venue{font-size:0.9em}}.music-event-details__img{position:relative;flex:6 1}.music-event-details__img img{height:100%;width:100%;min-height:300px;min-width:200px;object-fit:cover}@media only screen and (max-width: 1080px){.music-event-details__img{align-self:center !important;flex:60% 1 !important}.music-event-details__img img{height:auto !important;width:70% !important;margin:auto;display:block}}@media only screen and (max-width: 700px){.music-event-details__img{flex-direction:column-reverse}.music-event-details__img img{width:100% !important;min-height:180px}.music-event-details__img .overlay-background{background:linear-gradient(360deg, #252b40 6.73%, rgba(37,43,64,0) 33.05%) !important}}.music-event-details__btn{background-color:transparent;padding:8px 24px;border:solid #ffffff 1px;align-self:flex-start;margin-top:auto;box-shadow:0px 1px 2px rgba(0,0,0,0.25);transition:all 700ms}@media only screen and (min-width: 700px){.music-event-details__btn{cursor:pointer}.music-event-details__btn:hover{background-color:#161a27}}.overlay-background{position:absolute;width:100%;height:100%;background:linear-gradient(90deg, #252b40 6.73%, rgba(37,43,64,0) 33.05%)}

.music-event__wrapper{padding:32px;display:grid;grid-gap:10px;grid-template-columns:repeat(auto-fill, minmax(332px, 1fr));align-items:start;grid-auto-flow:dense;grid-gap:16px}@media only screen and (max-width: 621px){.music-event__wrapper{padding:16px;grid-template-columns:repeat(auto-fill, minmax(290px, 1fr));align-items:center}}

.app-footer{background:#252b40;padding:16px;text-align:center;margin-top:auto}

*{font-family:Segoe UI;color:white}html{scroll-behavior:smooth}body{overflow-x:hidden;height:100vh}.App{height:100vh;display:flex;flex-direction:column}

